import React, { Component} from "react";
import { Radio, Form, Input, Select, TextArea, Label, Button, Responsive, Breadcrumb, Container } from 'semantic-ui-react';
import ImageUploader from 'react-images-upload';
import Axios from "axios";
import { connect } from 'react-redux';
import Footer from '../../../includes/footer';
import ResponsiveContainer from '../../../includes/navbar';
import BreadCrumb from "../../../components/BreadCrumb";

class DesktopContainer extends Component{
    state = {
        loading: false
    };
    
    render(){
        return(
            <Responsive minWidth={Responsive.onlyTablet.minWidth}>
                {/* {BreadcrumbExample()} */}
                <Container>
                <BreadCrumb />
                <Form loading={ this.state.loading }>
                    <Form.Field
                        control={Select}
                        options={this.state.categoryList}
                        label={{ children: 'Category', htmlFor: 'form-select-control-gender' }}
                        placeholder='Category'
                        search
                        name='course_category_id'
                        value={this.state.course_category_id}
                        onChange={this.handleChange}
                        searchInput={{ id: 'form-select-control-gender' }}
                    />

                    <Form.Field
                        id='form-input-control-first-name'
                        control={Input}
                        label='Course Name'
                        placeholder='Course Name'
                        onChange={this.handleChange}
                        name='course_name'
                        value={this.state.course_name}
                    />

                    <Form.Field
                        id='form-input-control-first-name'
                        control={Input}
                        label='Course Price'
                        placeholder='Course Price'
                        onChange={this.handleChange}
                        name='course_price'
                        value={this.state.course_price}
                    />

                    <Form.Field>
                        <Radio toggle label="Drafted/ Publish"/>
                    </Form.Field>

                    <ImageUploader
                        withIcon={true}
                        singleImage={true}
                        withPreview={true}
                        buttonText='Choose Course image'
                        imgExtension={['.jpg', '.gif', '.png', '.gif']}
                        maxFileSize={5242880}
                        name='course_image'
                        onChange={this.onDrop}
                    />
                
                    <Form.Field
                        id='form-button-control-public'
                        control={Button}
                        content='Create Course'
                        style={{ backgroundColor: '#56BB47', color: '#ffffff' }}
                    />
                </Form>
                </Container>
            </Responsive>
        );
    }
}

